.gpt3__header {
    display: flex;
    font-family: var(--font-family);
    margin: 0 3rem 0 6rem;
}

.gpt3__header::before {
    content: "";
    width: 7px;
    height: 15px;
    background: rgba(27, 120, 222, 1);
    box-shadow: 0 0 14em 14em rgba(27, 120, 222, 1);
    filter: blur(12em);
    border-radius: 50%;
    position: relative;
    top: 80px;
    left: 15px;
    z-index: -1;
}

.gpt3__header-content_img::after {
    content: "";
    width: 7px;
    height: 15px;
    background: rgba(27, 120, 222, 0.9);
    box-shadow: 0 0 6em 6em rgba(27, 120, 222, .9);
    border-radius: 50%;
    position:relative;
    top: 90vh;
    right: 0;
    filter: blur(10em);
    z-index: -2;

    ;
}

.gpt3__header-content {
    display: flex;

    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}

.gpt3__header-content h1 {
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;

}

.gpt3__header-content p {
    font-style: normal;
    font-weight: normal;
    font-size:  20px;
    line-height: 27px;

    color: #81AFDD;
    margin: 1.5rem 0 0 0;
}

.gpt3__header-content_input {
    display: flex;
    padding: 2rem 0;
    width: 100%;
    margin-top: 1rem;
}

.gpt3__header-content_input * {
    height: 70px;
    border: 0;
    font-size: 20px;
    line-height: 1.7075em;
    color: #d3d3d3;
}

.gpt3__header-content_input input {
    background: var(--color-footer);
    flex: 1;
    border-radius: 5px 0 0 5px;

    outline: none;
    border: 2px solid var(--color-footer);
    padding-left: 16px;
}

.gpt3__header-content_input button {
    background: #ff4820;
    flex: 0.5;
    border-radius: 0 5px 5px 0;
    font-weight: bold;
    cursor: pointer;
    padding: 0 1 rem;
}

.gpt3__header-content_people {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.gpt3__header-content_people img {
    height: 38px;
}

.gpt3__header-content_people p {
    font-size: 12px;
    line-height: 38px;
    margin: 0 0 0 1rem;
    color: #d3d3d3;
}

.gpt3__header-content_img {
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.gpt3__header-content_img img{
    object-fit: scale-down;
    transition: all 0.5s ease-in-out;
}

.split {
    margin-inline: auto;
    object-fit: scale-down;
}

@media screen and (max-width: 74.75em) {
    .gpt3__header {
        flex-direction: column;
        margin: 0;
    }

    .gpt3__header-content {
        margin: 0 4rem;
    }

    .split {
        flex-basis: 100%;
    }

    .gpt3__header-content_img img {
        display: block;
        max-width: 100%;
    }
}

@media screen and (max-width: 43.75em) {
    .gpt3__header {
        margin: 0 2rem;
    }
    .gpt3__header-content {
        margin: 0 0 0.5rem 0;
    }

    .gpt3__header-content_people p {
        font-size: 1.8vw;
        line-height: 28px;
    }
    .gpt3__header-content_img img {
        display: block;
        max-width: 100%;
    }
}

@media screen and (max-width: 31.25em) {
    *{
        margin: 0;
    }
    .gpt3__header {
        margin: 0 2rem;
    }
    .gpt3__header-content {
        margin: 0 0 0rem 0;
    }


    .gpt3__header-content_input * {
        height: 50px;
    }
    .gpt3__header-content_input button {
        font-size: 16px;

    }

    .gpt3__header-content_people {
        position: relative;
        right: -21vw;
        flex-direction: column;
        justify-content: center;

    }

    .gpt3__header-content_people p {
        font-size: 1.8vw;
        align-items: center;
        margin: 0;
    }

}

.fade-in {
	-webkit-animation: fade-in 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: fade-in 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

