@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
@font-face {
  font-family: Gilroy-Medium;
  src: url(/landing-page-gpt3/static/media/Gilroy-Medium.ef9daf9c.ttf);
}

:root {
  --font-family: 'Manrope', sans-serif;
  --font-footer:  'Gilroy-Medium', 'Manrope', sans-serif;

  --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #f57457;
}
.gpt3__article-container {
    display: flex;
    flex: 1 1;
    flex-direction: column;
  
    width: 100%;
    height: 100%; 
   
    color: #fff;
}

.gpt3__article-container_image {
    display: flex;
    background: transparent;       
}

.gpt3__article-container_image img {      
    width: 100%;
    height: 100%;  
}

.gpt3__article-text {
    padding: 2em;
    height: 100%;
    display: flex;
    flex-direction: column;  
    justify-content: space-between;
}

.gpt3__article-text {
    background: #042C54;
}

.gpt3__article-text h1 {    
    
    font-size: 1.21em;
    margin-bottom: 4rem;
}


.gpt3__article-text p {
    font-size: 12px;
    font-weight: 700;
    line-height: 35px;
}

.gpt3__article-text p:last-child {
    cursor: pointer;
}
.gpt3__blog {
    display: flex;
    flex-direction: column;     
}

.gpt3__blog-heading h1 {
    font-size: 3.88em;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
    margin-right: 1em;
    margin-bottom: 1.3em;
}

.gpt3__blog-container {
  display: flex;
  flex-direction: row;     
}

.gpt3__blog-container_groupA {
    flex: 0.75 1;
    margin-right: 2em;

}

.gpt3__blog-container_groupA .gpt3__article-text h1 {
    font-size: 1.3em;
    margin-bottom: 18em;
}

.gpt3__container-groupB {
    flex: 1 1;
    display: grid;     
    grid-template-columns:  repeat(2, 1fr);;
    grid-template-rows:  repeat(2, 1fr) ;
    /* outline: 2px solid red; */
    grid-gap: 2em;
    gap: 2em;  
}

@media screen and (max-width: 850px) {    
    .gpt3__blog-container {     
        flex-direction: column;     
    }

    .gpt3__blog-container_groupA {       
        margin: 0 0 2em 0;
    }
}
* {
    font-size: 16px;
}

.gpt3__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
    
}

.gpt3__features-container__feature-title {
    flex: 1 1;
    max-width: 250px;
    margin-right: 2rem;
}

.gpt3__features-container__feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px #0007;
    margin-bottom: 0.25rem;
}

.gpt3__features-container__feature h1 {
    color: #fff;
    font-size: 1.5em;
    line-height: 1.5em;
    font-weight: 800;
    letter-spacing: -0.04rem;
    margin-bottom: 1em;
}

.gpt3__features-container_feature-text {
    display: flex;
    font-size: 1em;
    flex: 2 1;
    max-width: 390px;
    color: var(--color-text);
}


@media screen and (max-width:550px) {           
    .gpt3__features-container__feature-title div {
      position: relative;
      left: 0;
      display: flex;
      align-items: flex-start;
      justify-content: flex-start
    }

    .gpt3__features-container__feature > div {      
        margin: 1rem 0;
    }
}
.gpt3__features {
    font-family: var(--font-family);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%; 
    margin-bottom: -5em;
} 

.gpt3__features-heading::before {
    content: "";
    width: 7px;
    height: 15px;
    background: rgba(27, 120, 222, .49);
    box-shadow: 0 0 14em 14em rgba(27, 120, 222, .49);
    -webkit-filter: blur(8em);
            filter: blur(8em);
    border-radius: 50%;
    position: relative;
    top: 80px;
    left: 15px;
    z-index: -1;
}

.gpt3__features-heading {
    display: flex;
    flex: 1 1;
    flex-wrap: wrap;
    flex-direction: column;    
    align-items: flex-start;
    justify-content: flex-start;

    text-align: left;
    margin-right: 5em;
}

.gpt3__features-heading h1 {
    font-size: 2.125em;
    line-height: 45px;
    font-weight: 800;
    
    margin-bottom: 2em;
}

.gpt3__features-heading p {
    color: var(--color-subtext);
    margin-bottom: 2em;
    cursor: pointer;
}

.gpt3__features-container {
    display: flex;
    flex:1.5 1;
    justify-content: flex-start;
    flex-direction: column;
    flex-wrap: wrap;
    flex-direction: row;
}

.gpt3__features .gpt3__features-container__feature h1 {   
    font-size: 1.18em;  
}

@media screen and (max-width: 1200px) {
    .gpt3__features {
        flex-direction: column;
        font-size: 12px;       
    }
}


@media screen and (max-width: 850px) {
    .gpt3__features-heading {       
        margin: 0 2em 0 2em; 
        justify-content: center; 
    }    
}


@media screen and (max-width: 550px) {
    .gpt3__features-heading {   
        font-size: 1.32em;    
        margin: 0 0 2em 0; 
        justify-content: center; 
    }    
        
    .gpt3__features .gpt3__features-container__feature {   
        flex-direction: column;
    }

    .gpt3__features .gpt3__features-container__feature-title {   
        font-size: 1.20em;
    }

}



.gpt3__footer {
    background: var(--color-footer);
    color: #fff;
    margin-top: 6em;
}

.gpt3__footer p {
    font-size: 12px;
    line-height: 12.1px;
    font-family: var(--font-footer);
}

.gpt3__footer-heading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.gpt3__footer-heading h1{
    text-align: center;
    font-size: 3.88em;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
    margin-bottom: 1.3em;
}

.gpt3__footer-heading button {
    font-size: 12px;
    line-height: 14.1px;
    padding: 1.5em 2em;
    color: #fff;
    background: var(--color-footer);
    border: 1px solid #fff;
    cursor: pointer;
}


.gpt3__footer-links {
    display: flex;
    flex-direction: row;
    margin-top: 6em;
}

.gpt3__footer-links_heading {
    max-width: 190px;
    margin-right: 2em;
}

.gpt3__footer-links_heading p {
    margin-top: 1em;
}

.gpt3__footer-links-container {
    display: flex;
    flex: 1 1;
    flex-direction: row;  
}

.gpt3__footer-links-container div{
    max-width: 100px;
    flex: 1 1;
    margin-inline: auto;
}

.gpt3__footer-links-container p{
   margin-block: 2em;
}

.gpt3__footer-links-container_legality {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-top: 6em;
    margin-bottom: -2em;
}

@media screen and (max-width: 1000px) {
    .gpt3__footer-heading .section__padding {
        padding: 0;
    }
}

@media screen and (max-width: 650px) {
    .gpt3__footer h1 {
        font-size: 3em;
    }
    
}

@media screen and (max-width: 500px) {
    .gpt3__footer-links_heading {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        max-width: none;
        margin: 0 ;
    }

    .gpt3__footer-links_heading img {
        height: 100%;
        width: 100%;
        object-fit: scale-down;
    }
    .gpt3__footer-links_heading p {
        max-width: 170px;
        margin-top: -2.25em;
        text-align: center;
    }

    .gpt3__footer-links {
        flex-direction: column;   
        margin-top: 4em;    
    }   

    .gpt3__footer-links-container {
        margin-top: 3em;
    } 
}

.gpt3__header {
    display: flex;
    font-family: var(--font-family);
    margin: 0 3rem 0 6rem;
}

.gpt3__header::before {
    content: "";
    width: 7px;
    height: 15px;
    background: rgba(27, 120, 222, 1);
    box-shadow: 0 0 14em 14em rgba(27, 120, 222, 1);
    -webkit-filter: blur(12em);
            filter: blur(12em);
    border-radius: 50%;
    position: relative;
    top: 80px;
    left: 15px;
    z-index: -1;
}

.gpt3__header-content_img::after {
    content: "";
    width: 7px;
    height: 15px;
    background: rgba(27, 120, 222, 0.9);
    box-shadow: 0 0 6em 6em rgba(27, 120, 222, .9);
    border-radius: 50%;
    position:relative;
    top: 90vh;
    right: 0;
    -webkit-filter: blur(10em);
            filter: blur(10em);
    z-index: -2;

    ;
}

.gpt3__header-content {
    display: flex;

    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}

.gpt3__header-content h1 {
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;

}

.gpt3__header-content p {
    font-style: normal;
    font-weight: normal;
    font-size:  20px;
    line-height: 27px;

    color: #81AFDD;
    margin: 1.5rem 0 0 0;
}

.gpt3__header-content_input {
    display: flex;
    padding: 2rem 0;
    width: 100%;
    margin-top: 1rem;
}

.gpt3__header-content_input * {
    height: 70px;
    border: 0;
    font-size: 20px;
    line-height: 1.7075em;
    color: #d3d3d3;
}

.gpt3__header-content_input input {
    background: var(--color-footer);
    flex: 1 1;
    border-radius: 5px 0 0 5px;

    outline: none;
    border: 2px solid var(--color-footer);
    padding-left: 16px;
}

.gpt3__header-content_input button {
    background: #ff4820;
    flex: 0.5 1;
    border-radius: 0 5px 5px 0;
    font-weight: bold;
    cursor: pointer;
    padding: 0 1 rem;
}

.gpt3__header-content_people {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.gpt3__header-content_people img {
    height: 38px;
}

.gpt3__header-content_people p {
    font-size: 12px;
    line-height: 38px;
    margin: 0 0 0 1rem;
    color: #d3d3d3;
}

.gpt3__header-content_img {
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.gpt3__header-content_img img{
    object-fit: scale-down;
    transition: all 0.5s ease-in-out;
}

.split {
    margin-inline: auto;
    object-fit: scale-down;
}

@media screen and (max-width: 74.75em) {
    .gpt3__header {
        flex-direction: column;
        margin: 0;
    }

    .gpt3__header-content {
        margin: 0 4rem;
    }

    .split {
        flex-basis: 100%;
    }

    .gpt3__header-content_img img {
        display: block;
        max-width: 100%;
    }
}

@media screen and (max-width: 43.75em) {
    .gpt3__header {
        margin: 0 2rem;
    }
    .gpt3__header-content {
        margin: 0 0 0.5rem 0;
    }

    .gpt3__header-content_people p {
        font-size: 1.8vw;
        line-height: 28px;
    }
    .gpt3__header-content_img img {
        display: block;
        max-width: 100%;
    }
}

@media screen and (max-width: 31.25em) {
    *{
        margin: 0;
    }
    .gpt3__header {
        margin: 0 2rem;
    }
    .gpt3__header-content {
        margin: 0 0 0rem 0;
    }


    .gpt3__header-content_input * {
        height: 50px;
    }
    .gpt3__header-content_input button {
        font-size: 16px;

    }

    .gpt3__header-content_people {
        position: relative;
        right: -21vw;
        flex-direction: column;
        justify-content: center;

    }

    .gpt3__header-content_people p {
        font-size: 1.8vw;
        align-items: center;
        margin: 0;
    }

}

