.gpt3__brands {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 70vw;
    position: relative;
    right: -15vw;
    margin-top: 2em;
}

.gpt3__brands * {
    max-height: 2vw;
    margin-inline: auto;
}


@media screen and (max-width: 700px) {
    .gpt3__brands * {   
        max-height: 2.5vw;    
      
    }      
}

@media screen and (max-width: 500px) {
    .gpt3__brands * {   
        max-height: 3vw;    
        margin: 1em;
    }      
}
.gpt3__cta {
   display: flex;
   align-items: center;
   justify-content: flex-start;
   font-family: var(--font-family);
}

.gpt3__cta-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
 
    background: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
    border: 1px solid #000000;
    box-shadow: 0 0 4px #0007;
    border-radius: 0.8rem;
    padding: 2em;
    width: 100%;
}

.gpt3__cta-container-text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.gpt3__cta-container-text p {
    font-size: 0.75em;
    cursor: pointer;
    color: #0e0e0e;
}

.gpt3__cta-container-text h2 {
    font-size: 1.5em;
    font-weight: 800;
    line-height: 45px;
    flex: 1 1;
    margin: 0.46em 1em 0 0;
}

.gpt3__cta-container-button {   
   display: flex;
   align-items: center;
}

.gpt3__cta-container-button button{   
    font-size: 1.128em;
    line-height: 30px;
    font-weight: 700;
    padding: 0.85em 2em;
    border-radius: 40px;
    background: rgb(6, 5, 14);
    color: #fff;
    border: 0;
    cursor: pointer;
    min-width: 170px;
}

@media screen and (max-width: 850px) {
    .gpt3__cta-container {  
        flex-direction: column;
        justify-content: center;   
        padding: 1.1em;
    }
        
    .gpt3__cta-container-button {   
        width: 100%;
        display: flex;
        justify-content: center;
        margin: 1em 0 0 0;
    }

    .gpt3__cta-container-text h2 {
        line-height: 30px;
        margin: 1em 0 0 0;
        text-align: center;
    }

}


.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: var(--font-family);
    color: #d3d3d3;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    padding: 2rem 6rem;
}

.gpt3__navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__navbar-links_logo {
    margin-right: 2rem;
}

.gpt3__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
}

.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
}
.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;   
}

.gpt3__navbar-links_container p, 
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
    padding: 1rem 2rem;    
    background: #ff4820;   
    color: #d3d3d3;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
}

.gpt3__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
} 

.gpt3__navbar-menu svg {
    cursor: pointer;
}

.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem; 
    position: absolute;
    top: -20px; 
    right: 25px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5px #000700;
}

.gpt3__navbar-menu_container p {
    margin: 0.5rem 0;
    padding: 0.5rem 0;
}

.gpt3__navbar-menu_container p:hover {
    box-shadow: 0 0 2.5px .5px #d3d3d3 inset;
    border-radius: 5px;
    
}


.gpt3__navbar-menu_container-links-sing {
    display: none;
}


@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container {
        display: none;
    }

    .gpt3__navbar-menu {
        display: flex; 
    }
    .gpt3__navbar-links_logo img {
        width: 100%;
        height: 100%;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__navbar {
        padding: 2rem 4rem;
    }

    
}


@media screen and (max-width: 500px) {
    .gpt3__navbar {
        padding: 2rem;
    }

    .gpt3__navbar-sign {
        display: none;
    }

    .gpt3__navbar-menu_container {
        top: -25px;
    right: 25px;
    }

    .gpt3__navbar-menu_container-links-sing {
        display: block;
    }
}
 
* {
margin: 0;
padding: 0;
box-sizing: border-box;
scroll-behavior: smooth;
}

body {
  background: var(--color-bg);
  font-family: var(--font-family);
  margin: 0;
}

a {
  color: unset;
  text-decoration:  none;
}


.gradient__bg {
    /* ff 3.6+ */

  /* safari 5.1+,chrome 10+ */

  /* opera 11.10+ */

  /* ie 10+ */

  /* global 92%+ browsers support */
  background:radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);

}


.gradient__text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section__padding {
  padding: 4em 6em;
}

.section__margin {
  margin: 4em 6em;
}



.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



@media screen and (max-width: 700px) {
  .section__padding {
    padding: 3em;
  }
  .section__margin {
    margin: 3em;
  }
}

@media screen and (max-width: 550px) {
  * {
    font-size: 14px;
  }
  .section__padding {
    padding: 3em 2em;
  }
  .section__margin {
    margin: 3em 2em;
  }
}    
  


::-webkit-scrollbar {
  background: #bbb6b6;
  box-shadow: 0 0 4px .3px var(--color-subtext) inset;
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background: var(--color-subtext);  
  box-shadow: 0 0 4px .5px #c7bbbb inset;
  border-radius: 5px;
}
